import "./TopPanel.css";

const TopPanel = (props) => {
  return (
    <div className="top-panel container d-flex justify-content-center">
      <div className="row">
        {/* left side */}
        <div id="logo" className="w-50">
          <p>2048</p>
          <p className="phrase">How high can you get?</p>
        </div>
        {/* right side */}
        <div className="d-flex flex-column align-items-end w-50">
          <div className="score-div">
            <p id="score-label">SCORE</p>
            <p id="score-counter">1425</p>
          </div>
          <div>
            <button
              type="button"
              id="btn-new-game"
              className="btn"
              onClick={props.onNewGameClick}
            >
              NEW GAME
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default TopPanel;
